@import '~antd/dist/antd.css';
:root {
  --navbar-height: 47px;
  box-sizing: border-box;
}


.site-layout-content {
  
  padding: 24px;
  min-height: 280px;
}

.header-fixed {
  position: fixed; /* Set the navbar to fixed position */
  width: 100%;
  z-index: 1;
}

.layout-content-main {
  padding-top: calc(var(--navbar-height) + 10px) ;
}

.dark-mode-switch {
  margin: 10px;
}

.article {
  padding: 5px;
  min-width: 100%;
}

.article--card {
  height: 100%;
}

.page-selector {
  align-content: center;
}
.news--header {
  padding: 5px;
  text-align: center;
  min-width: 100%;
}

.loading-content {
  font-size: xx-large;
  position: absolute;
  z-index: 1;
  left: 50%;
  top:50%;

}

.loading-text {
  position: relative;
  left: -50%;
  top: -50%;
  text-align: center;

}

.page-selector {
  display: table;
  margin: 0 auto;
}

.dark {

}
@media only screen and (min-width: 768px) {
  .layout {
    min-height: 100vh;
  }
  .layout-content-main {
    padding: calc(var(--navbar-height) + 20px) 50px 0 50px;
  }

  .dark-mode-switch {
    margin: 10px;
    float: right;
  }
  .article {
    min-width: 1vw;
  }

  .news--header {
    min-width: 50%;
  }
}